import React from "react";
import "./style.css";

class Contact extends React.Component {
  handleSend = () => {
    let title = document.getElementById("title-mailer").value;
    let body = document.getElementById("body-mailer").value;

    window.open(
      `mailto:wypoczynkowe.rewa@gmail.com?subject=${title}&body=${body}`
    );
  };

  render() {
    return (
      <div id="contact">
        <div id="main">
          <h1>Dane kontaktowe:</h1>
          <div id="mailer">
            <div id="button-mailer" onClick={this.handleSend}>
              Wyślij
            </div>
            <input
              id="title-mailer"
              type="email"
              placeholder="Tytuł wiadomości"
            />
            <textarea id="body-mailer" />
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;
