import React from "react";
import ImageGallery from "react-image-gallery";
import "../../../node_modules/react-image-gallery/styles/css/image-gallery.css";
import "./style.css";
import data from "../../data/houses";
import PersonIcon from "@material-ui/icons/Person";
import nextId from "react-id-generator";

class Photos extends React.Component {
  state = {
    all: data
  };

  renderPeople = () => {
    return <PersonIcon key={nextId()} />;
  };

  rep = x => {
    let test = [];
    while (x > 1) {
      x--;
      test.push(this.renderPeople());
    }

    if (x === 1) {
      test.push(this.renderPeople());
    }

    return test.map(element => {
      return element;
    });
  };

  renderAll = () => {
    return this.state.all.map((element, index) => {
      let photos = [];
      let cl = "";
      let el = "";

      if (index % 2 === 0) {
        cl = "main main-1";
        el = "main el-1";
      } else {
        cl = "main main-2";
        el = "main el-2";
      }

      if (window.innerWidth < 451) {
        photos = element.device;
      } else {
        photos = element.original;
      }
      return (
        <div className="house item" key={nextId()}>
          <div className={`title item item-${index % 2}`}>
            <b className={el}>{element.title}</b>
          </div>
          <div className={`people item item-${index % 2}`}>
            <b className={el}>{this.rep(element.people)}</b>
          </div>
          <div className={`costs item item-${index % 2}`}>
            <b className={el}>{element.costs}</b>
          </div>
          <div className={cl}>
            <ImageGallery items={photos} key={nextId()} />
            <div className={`description  description-${index % 2}`}>
              <b>{element.description}</b>
            </div>
          </div>
        </div>
      );
    });
  };

  render() {
    return (
      <div id="photos">
        <div id="gallery">
          <h1>Nasza propozycja:</h1>
          {this.renderAll()}
        </div>
      </div>
    );
  }
}

export default Photos;
